import route from './route'

export default {
  name: route.key,

  data() {
    return {
      filter: {
        search: null
      },
      route,
    }
  },

  computed: {
    data() {
      return [
        {
          draw: '3D 9PM',
          schedule: '21:00',
          opening_time: '16:30',
          closing_time: '20:30',
          date_created: '2021-03-26T12:11:50.058Z'
        }
      ]
    }
  },

  methods: {
    async init() {

    }
  }
}



